import * as React from 'react'
import {Link} from 'react-router-dom'
import {R} from '../../../navigation/routes'


export default function Header() {
    return (
        <header>
            <ul>
                <li>
                    <Link to={R.HOME}></Link>
                </li>
                <li>
                    <Link to={R.CATALOG}></Link>
                </li>
                <li>
                    <Link to={R.ABOUT}> </Link>
                </li>
                <li>
                    <Link to={R.DELIVERY}></Link>
                </li>
            </ul>
        </header>
    )
}